.image-grid {
	width: 100%;
	height: 100%;
	position: relative;
	@apply relative hidden lg:grid grid-cols-6 w-full h-full;
}

.image-container {
	position: sticky;
	top: 30vh;
	img {
		pointer-events: none;
		opacity: 0;
		transition: all 0.6s;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);

		&.active-img {
			opacity: 1;
		}
	}
}

.m-top-grid {
	padding-top: 30vh;
}

.image-container {
	img {
		max-height: 50vh;
	}
}

.page-content {
	@apply px-3 lg:grid grid-cols-2 gap-6 pb-20 lg:pb-28;
	padding-top: 20vh;
}

@screen lg {
	.page-content {
		padding-top: 30vh;
	}
}
