.home-wrapper {
	padding: 20vh 0 50px;
	height: 100%;
	width: 100%;
}

.homepage-grid {
	@apply grid grid-cols-1 lg:grid-cols-12 gap-6;
}

.passport-photo {
	max-height: 145px;
}

.homepage-container {
	@apply mx-auto max-w-[578px] lg:max-w-full px-3;
}

.portrait-stamp {
	position: absolute;
	top: 0;
	right: 0;
	transform: translate(50%, -50%);
}

.passport {
	&-heading {
		max-width: 75px;
		width: 100%;
	}
	&-detail {
	}
}

.biography {
	max-width: 450px;

	a {
		@apply border-b;
		border-color: initial;

		&:hover {
			@apply border-b-0;
		}
	}
}

@screen lg {
	.home-wrapper {
		@apply pt-0;
		height: 100%;
		width: 100%;
		min-height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.passport-photo {
		max-height: 265px;
	}
}
